@import url(https://fonts.googleapis.com/css2?family=Zilla+Slab:ital,wght@0,500;1,300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Pacifico&family=Zilla+Slab:ital,wght@0,500;1,300&display=swap);
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
  color: darkgray;
}

body {
  font-family: "Pacifico", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #282c34;
  color: white;
  max-width: 1100px;
  margin: 0 auto;
}

.App{padding:20px;text-align:left}.won-card{background-color:green}.lost-card{background-color:#e15233}

.MatchDetailCard{display:grid;grid-template-columns:1fr 1fr;padding:10px;margin-bottom:20px;border-radius:2rem}.MatchDetailCard .match-date,.MatchDetailCard .match-venue,.MatchDetailCard .match-result{margin-top:10px}.MatchDetailCard img{width:auto;height:40px;margin-right:5px}.MatchDetailCard .additional-detail{text-align:right}.MatchDetailCard .additional-detail>p{margin-bottom:20px}

.MatchSmallCard{padding:10px;margin-bottom:20px;border-radius:1rem;height:75%}.MatchSmallCard .match-result{margin-top:10px}

.TeamPage{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:1fr 1fr 1fr;grid-column-gap:10px;grid-row-gap:10px}.TeamPage .team-name-section{grid-column:span 3;justify-self:start;align-self:center}.TeamPage .team-name-section img{height:150px;width:auto}.TeamPage .win-loss-section{justify-self:center;padding:40px;text-align:center;font-size:1.5rem}.TeamPage .team-name{font-size:4rem;margin-bottom:20px}.TeamPage .match-detail-section{grid-column:span 4}.TeamPage .more-link{-webkit-align-self:center;align-self:center;justify-self:center;font-size:1.5rem}

.MatchPage{display:grid;grid-template-columns:1fr 6fr;grid-column-gap:20px}.MatchPage .year-selector{justify-self:end;padding:20px}.MatchPage .page-heading{margin:10px auto}

.YearSelector{text-align:center;list-style-type:none}.YearSelector li{margin-top:10px;margin-bottom:10px}.YearSelector .selected-year{font-size:25px}.YearSelector .selected-year a{color:white}

.TeamTile{margin-bottom:20px;font-size:20px;padding:20px;text-align:center;border:1px solid white;border-radius:1rem}.TeamTile img{height:150px;width:auto}

.HomePage .team-grid{display:grid;grid-template-columns:1fr 1fr;grid-column-gap:20px;grid-row-gap:10px}

.HeaderSection{display:grid;grid-template-columns:1fr 7fr;padding:40px;margin-bottom:40px;background-color:green}.HeaderSection .app-name{text-align:center;font-size:40px}.HeaderSection img{height:60px}

